<template>
    <v-card class="pa-5 text-center" hover>
        <v-img :src="img_src" width="150"  :aspect-ratio="1/1"   class="mb-3  ma-auto">
        </v-img>
        <p class="title text-center text-capitalize"> {{ heading }} </p>
        <p class="subtitle-1 text-center my-0"> {{ subheading }} </p>
        <v-list disabled color="transparent ma-0 " dense>
            <v-list-item-group color="primary">
                <v-list-item
                    v-for="(feature, i) in features"
                    :key="i"
                    >
                <v-list-item-content>
                    <v-list-item-title >
                        <v-icon color="success" >mdi-marker-check</v-icon> {{ feature }} </v-list-item-title>
                </v-list-item-content>
                </v-list-item>
            </v-list-item-group>
            </v-list>
        <p class="overline"><span>From</span> <span>kes </span><span class="caption primary--text"> {{ amount }} </span></p>
        
        <template>
    <v-dialog
      v-model="dialog"
      width="500"
    >
      <template v-slot:activator="{ on }">
        <v-layout class="px-5" >
            <v-btn color="primary" v-on="on" :href="select_link" block>Explore</v-btn>
        </v-layout>
      </template>

      <v-card>
        <v-card-title
        
          class="headline d-flex align-center justify-center grey lighten-2 text-center"
          primary-title
        >
          {{ heading }}
        </v-card-title>

        <v-card-text>
            <p class="headline text-center">To get the {{ heading }} website call <v-btn text x-large outlined>0712700487 </v-btn>
            </p>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            text
            @click="dialog = false"
          >
            close
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>
        
    </v-card>
</template>
<script>
export default {
    name:'pricingCard',
    props:[
       'img_src', 'heading', 'subheading','features','amount','select_link'
    ],
    data(){
        return{
            dialog: null
        }
    }
}
</script>
<style lang="scss" scoped>

</style>